<template>
    <!-- 选择显示的位置 -->
    <u-popup round="40rpx" :show="show" @close="close" @open="open">
        <view
        style="position: relative;"
        >
            <view class="box">
                <view style="height: 20rpx;"></view>
                <goods-card-x :bodyData="childrenSelect.id != null ? childrenSelect : bodyData"></goods-card-x>
                <view style="height: 20rpx;"></view>
            </view>
            <scroll-view :scroll-y="true" :style="'height: calc(100vh - ' + navBarHeight + 'px - 260rpx)'">
                <view style="padding: 20rpx;
            box-sizing: border-box;
            ">
                    <view class="scroll-box-title">收货地址

                        <image @click="toEditAddress" :src="$kt.file.byPath('image/icon/edit.svg')" :style="{
                            width: '40rpx',
                            height: '40rpx',
                        }" style="float: right;" class="icon-btn"></image>
                    </view>

                    <goods-default-address 
                    @flush="flushAddress"
                    ref="goodsAddress"></goods-default-address>
                </view>

                <view v-if="childrenList.length > 0" style="padding: 0 20rpx 20rpx 20rpx;
            box-sizing: border-box;
            ">
                    <view class="scroll-box-title">商品选项</view>
                    <view>
                        <view v-for="(item, index) in childrenList" :key="index" @click="toSelectChildren(item)"
                            class="childer-goods-box" :style="{
                                'border': childrenSelect.id == item.id ? '3rpx solid #333' : '1rpx solid #eee',
                            }">
                            <goods-card-x :bodyData="item"></goods-card-x>
                        </view>

                    </view>

                </view>

                <view style="height: 120rpx;"></view>

            </scroll-view>

            <view
            class="exchange-box"
            >
            
            <kt-button
            @click="submit"
            :borderRadius="0"
            ref="submitBtn"
            >立即兑换</kt-button>

            </view>


        </view>
    </u-popup>
</template>

<script>
export default {
    data() {
        return {
            show: false,
            navHeight: 44,
            statusBarHeight: uni.getSystemInfoSync().statusBarHeight,
            navBarHeight: 48,
            ovoUserSelf: {
            },
            address: null,
            isLoadingAddress: false,
            bodyData: null,
            childrenSelect: {
                id: null
            },
            childrenList: [],
        }
    },
    created() {
        this.ovoUserSelf = this.$ovo.getSelf();
        uni.$on('ovoUserSelf', (res) => {
            if (this.ovoUserSelf == null || this.ovoUserSelf.location == null) {
                this.ovoUserSelf = res;
                return false;
            }
            if (res.location.latitude != this.ovoUserSelf.location.latitude && res.location.longitude != this.ovoUserSelf.location.longitude) {
                this.ovoUserSelf = res;
            }
            this.ovoUserSelf = res;

        });
    },
    mounted() {
        this.navHeight = 44;
        // #ifdef MP-WEIXIN
        this.navHeight = uni.getSystemInfoSync().system.indexOf("ios") != -1 ? 44 : 48;
        // #endif
        this.navBarHeight = this.navHeight - (-this.statusBarHeight);
    },
    methods: {
        flushAddress(res) {
            console.log(JSON.stringify(res),"-------------------");
            this.address = res;
        },
        submit(){
            this.$refs.submitBtn.loading();
            var goodsId=this.bodyData.id;


            if(this.childrenList.length!=0 && this.childrenSelect.id == null){
                this.$refs.submitBtn.error('请选择商品选项');
                return false;
            }

            if(this.childrenList.length!=0){
                goodsId=this.childrenSelect.id;
            }

            if(this.address==null||this.address.id==null){
                this.$refs.submitBtn.error('请选择收货地址');
                return false;
            }

            this.$kt.request.request({
                api: this.$api.business.ovoShop.goods.exchange,
                data:{
                    goodsId:goodsId,
                    addressId:this.address.id,
                    count:1
                },
                stateSuccess: (res) => {
                    this.$refs.submitBtn.success('兑换成功');
                    this.getBalanceSelfMap();
                },
                stateFail: (res) => {
                    if(res.stateCode=='charmPointsNotEnough'){
                        this.$refs.submitBtn.error('情趣碎片不足');
                    }
                },
            });

        },
        getBalanceSelfMap() {
            this.$kt.request.request({
                api: this.$api.system.balance.getSelfMap,
                isNoInterceptor: true,
                stateSuccess: (res) => {
                    this.$system.setBalanceMap(res.data);
                },
            });
        },

        toSelectChildren(res) {
            this.childrenSelect = res;
        },
        open(bodyData) {
            this.show = true;
            if (bodyData == null) {
                return false;
            }

            this.bodyData = bodyData;

            this.childrenSelect = {
                id: null
            };

            this.childrenList = this.bodyData['children'];
            if (this.bodyData['children'] == null) {
                this.childrenList = [];
            }
        },
        close() {
            this.show = false;
        },
        toEditAddress() {
            // #ifdef MP-WEIXIN

            uni.chooseAddress({
                success: (res) => {
                    console.log(JSON.stringify(res));
                    this.$kt.request.request({
                        api: this.$api.system.address.save,
                        data: {
                            userName: res.userName,
                            telNumber: res.telNumber,
                            provinceName: res.provinceName,
                            cityName: res.cityName,
                            countyName: res.countyName,
                            detailInfo: res.detailInfo,
                            nationalCode: res.nationalCode,
                            postalCode: res.postalCode,
                        },
                        stateSuccess: (res) => {
                            this.$refs.goodsAddress.setAddress(res.data);
                        },
                        fail: (error) => { }
                    });

                },
                fail: (error) => { }
            })

            // #endif

        },
    }
}
</script>

<style lang="scss" scoped>

.exchange-box{
    position: absolute;
    bottom: 0;
    width: 100%;
    background-color: rgba(0,0,0,0);
    box-sizing: border-box;
}

.box {
    padding: 30rpx;
    background-color: #fff;
    border-radius: 30rpx;
}

.box-title {
    font-size: 30rpx;
    text-align: center;
    color: #999;
}

.select-box {
    border-radius: 30rpx;
    padding: 5rpx 20rpx 5rpx 20rpx;
    background-color: #f0f0f0;
    margin-bottom: 20rpx;

    .select-box-item {
        padding: 20rpx 0;
        display: flex;
        // title在左边，text在右边
        justify-content: space-between;

        .select-box-item-title {
            font-size: 28rpx;
            color: #999;
        }

        .select-box-item-text {
            font-size: 28rpx;
            color: #333;
        }
    }

}


.select-box-2 {
    border-bottom: 1px solid #eee;
    padding: 30rpx 0;

    .select-box-item {
        .select-box-item-title {
            font-size: 28rpx;
            color: #333;
            margin-bottom: 10rpx;
        }

        .select-box-item-text {
            font-size: 28rpx;
            color: #999;
        }
    }
}

.select-box-2:active {
    opacity: .7;
}


.scroll-box-title {
    font-size: 28rpx;
    color: #333;
}

.icon-btn:active {
    transform: scale(0.9);
}

.childer-goods-box {
    transform: scale(0.9);
    border-radius: 20rpx;
    border: 1px solid #eee;
    padding: 10rpx;
}

.childer-goods-box-active {
    border: 1px solid #999;
}
</style>
